#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy_woofc-cart-summary ul li .label,
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy_woofc-cart-summary ul li .value {
  color: #444444;
  font-size: 15px;
  font-weight: normal;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy_woofc-cart-summary ul li:not(:last-child) {
  border-bottom: 1px dotted var(--adfy_woofc_border_color);
  padding-bottom: 10px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy_woofc-cart-summary ul li.discount .value {
  color: red;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy_woofc-cart-summary ul li.total .value {
  font-weight: bold;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions .adfy__woofc-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 20px 15px;
  height: auto;
  border-radius: 4px;
  width: 100%;
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions .adfy__woofc-button {
    padding: 15px 10px;
  }
}
#adfy__floating-cart .adfy_woofc-inner .adfy__woofc-colophon .adfy__woofc-actions .continue-shopping {
  fill: #878787;
  color: #878787;
  background: #CFCFCF;
}

#adfy__floating-cart[data_type=drawer] {
  top: 0;
  bottom: 0;
  height: 100%;
  min-height: 100vh;
  width: 500px;
  opacity: 1;
  visibility: visible;
  /* Wide */
}
@media (max-width: 575px) {
  #adfy__floating-cart[data_type=drawer] {
    width: 93vw;
  }
}

/* -------------------------------------------------------------------------- 
	#. Position
  -------------------------------------------------------------------------- */
#adfy__floating-cart[data_type=modal] {
  z-index: var(--adfy_woo_floating_cart_zindex);
}

#adfy__floating-cart[data_type=drawer][data_position=left] {
  right: auto;
  left: -100vw;
}

#adfy__floating-cart[data_type=drawer][data_position=right] {
  left: auto;
  right: -100vw;
}

/* -------------------------------------------------------------------------- 
	#. Visiblity
  -------------------------------------------------------------------------- */
.adfy__woofc-visible #adfy__floating-cart {
  z-index: var(--adfy_woo_floating_cart_zindex);
  box-shadow: 10px 0px 30px rgba(0, 0, 0, 0.2);
}

.adfy__woofc-visible #adfy__floating-cart[data_type=drawer][data_position=left] {
  right: auto;
  left: 0;
  animation: adfyFloatingCartSlideToRight 0.6s ease;
}

.adfy__woofc-visible #adfy__floating-cart[data_type=drawer][data_position=right] {
  left: auto;
  right: 0;
  animation: adfyFloatingCartSlideToLeft 0.6s ease;
}

/* -------------------------------------------------------------------------- 
	#. Coupon container inside Floating Cart
  -------------------------------------------------------------------------- */
#adfy__woofc-coupon-container {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: 0;
  padding: 30px;
  width: 100%;
  height: 100%;
  z-index: calc(var(--adfy_woo_floating_cart_zindex) - 1);
  background-color: white;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
}
#adfy__woofc-coupon-container .coupon-container-header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-bottom: 15px;
  margin-bottom: 30px;
  border-bottom: 1px solid var(--adfy_woofc_border_color);
  /* Wide */
}
@media (max-width: 991px) {
  #adfy__woofc-coupon-container .coupon-container-header {
    justify-content: flex-end;
  }
}
#adfy__woofc-coupon-container .coupon-container-header #adfy__woofc-hide-coupon-container {
  display: inline-flex;
  font-size: 14px;
  line-height: 1;
  font-weight: normal;
  gap: 10px;
  align-items: center;
  fill: #444444;
  color: #444444;
}
#adfy__woofc-coupon-container .coupon-container-header #adfy__woofc-hide-coupon-container svg {
  width: 20px;
  height: 20px;
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 0;
  margin: 0;
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs {
  padding: 20px;
  border-radius: 5px;
  border: 2px dashed var(--adfy_woofc_border_color);
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs label {
  display: block;
  font-weight: normal;
  font-size: 14px;
  margin: 0 0 8px 0;
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #9E9E9E;
  opacity: 1; /* Firefox */
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs :-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #9E9E9E;
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs ::-ms-input-placeholder { /* Microsoft Edge */
  color: #9E9E9E;
}
#adfy__woofc-coupon-container #adfy__woofc-coupon-form .adfy__woofc-coupon-inputs .adfy__woofc-button {
  width: 100%;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
}

#adfy__woofc-coupon-container[data_display=visible] {
  opacity: 1;
  visibility: visible;
  z-index: calc(var(--adfy_woo_floating_cart_zindex) + 10);
}
#adfy__woofc-coupon-container[data_display=visible] #adfy__woofc-coupon-form {
  animation: adfyFloatingCartFadeDown 0.5s ease-in;
}

/* -------------------------------------------------------------------------- 
	#. Floating Cart Mask
  -------------------------------------------------------------------------- */
#adfy__woofc-overlay {
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s linear;
}

.adfy__woofc-visible #adfy__woofc-overlay {
  opacity: 1;
  visibility: visible;
  z-index: var(--adfy_woofc_overlaymask_zindex);
}

/* -------------------------------------------------------------------------- 
	#. Utility Class
  -------------------------------------------------------------------------- */
.adfy__hide-woofc {
  cursor: pointer;
}

.adfy__woofc-visible {
  overflow: hidden;
}

.adfy__woofc-link.has-underline {
  position: relative;
  text-decoration: none;
  color: var(--adfy_woofc_primary_color);
  transition: all 0.5s ease;
}
.adfy__woofc-link.has-underline::after, .adfy__woofc-link.has-underline::before {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: currentColor;
}
.adfy__woofc-link.has-underline::before {
  content: "";
  z-index: 1;
  background-color: currentColor;
  transform-origin: right center;
  transform: scaleX(0);
  will-change: scale;
  transition: transform 0.26s cubic-bezier(0.25, 0.8, 0.25, 1);
  -webkit-backface-visibility: hidden;
}
.adfy__woofc-link.has-underline::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 3px;
  opacity: 0.6;
  background-color: currentColor;
}
.adfy__woofc-link.has-underline:hover {
  color: var(--adfy_woofc_secondary_color);
  text-decoration: none;
}
.adfy__woofc-link.has-underline:hover::before {
  content: "";
  transform: scale(1);
  transform-origin: left center;
  background-color: var(--adfy_woofc_secondary_color);
}
.adfy__woofc-link.has-underline:focus {
  outline: none;
  text-decoration: none;
}

/* -------------------------------------------------------------------------- 
	#. Fade Down
  -------------------------------------------------------------------------- */
@keyframes adfyFloatingCartFadeDown {
  0% {
    opacity: 0;
    transform: translateY(15px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
/* -------------------------------------------------------------------------- 
	#. Slide to left
  -------------------------------------------------------------------------- */
@keyframes adfyFloatingCartSlideToLeft {
  from {
    right: -100vw;
    opacity: 0;
  }
  to {
    right: 0;
    opacity: 1;
  }
}
/* -------------------------------------------------------------------------- 
	#. Slide to right
  -------------------------------------------------------------------------- */
@keyframes adfyFloatingCartSlideToRight {
  from {
    right: 0;
    opacity: 1;
  }
  to {
    right: -100vw;
    opacity: 0;
  }
}